  import axios from 'axios'
  import flashModal from '~/components/flashmodal.vue'
  export default {
    layout:'login', 
    components: {
      flashModal
    },
    data () {
      return {
        id: '',
        password:'',
        company: {
         name:'VETEC'
        },
        modalMessage: {
          msg :'',
          show: false
        },//로그인 안내모달에 사용할 msg
      }
    },
    mounted: function() {
      document.body.className = 'login-body';
      this.urlChk();    
      this.roleChk();
    }, 
    destroyed() { 
      document.body.className = '';
    },
    methods: {
      async login () {
        if(this.id === null || this.id ==="") {
          alert("ID를 입력해주세요");
          this.$refs.id.focus()
          return
        }else if(this.password === null || this.password ==="") {
          alert("패스워드를 입력해주세요");
          this.$refs.password.focus()
          return
        }
        try {
          await this.$store.dispatch('login', {
            id: this.id,
            password: this.password
          }).then(() => {
            if(this.$store.getters.length != 0) {
              this.redirect()
            }else {
              console.log(this.$store.getters.ID)
            }
          })
          .catch((e)=> {
            console.log(e)
            this.modalMessage.msg = e.message
            this.modalMessage.show = true
          })
        } catch (e) {
            console.log(e)
          this.modalMessage.msg = e.message
          this.modalMessage.show = true
        }
      },
      urlChk() {

        var currentUrl = window.location.host
        
        if(currentUrl === "peak.aistier-bom.com") {
          this.company.name = "AIStier"
        }else {
          this.company.name = "VETEC"
        }
      
      },
      roleChk() {
        var roleChk = this.$route.query.code
        if(roleChk) {
          this.modalMessage.msg = "해당솔루션 권한이 없습니다."
          this.modalMessage.show = true 
          this.$router.push('/login')
        } 
      },
      redirect () {
        this.$router.push('/dashboard')
      }
  }
}
